/* Color */
:root {
  /* color */
  --background-brand-default: var(--brand-surface);
  --background-brand-hover: var(--brand-900);
  --background-brand-primary: var(--brand-primary);
  --background-brand-secondary: var(--brand-background);
  --background-brand-secondary-hover: var(--brand-300);
  --background-brand-tertiary: var(--brand-surface-variant);
  --background-brand-tertiary-hover: var(--brand-200);
  --background-danger-default: var(--red-500);
  --background-danger-hover: var(--red-600);
  --background-danger-secondary: var(--red-200);
  --background-danger-secondary-hover: var(--red-300);
  --background-danger-tertiary: var(--red-100);
  --background-danger-tertiary-hover: var(--red-200);
  --background-default-default: var(--white-1000);
  --background-default-default-hover: var(--gray-100);
  --background-default-secondary: var(--gray-100);
  --background-default-secondary-hover: var(--gray-200);
  --background-default-tertiary: var(--gray-300);
  --background-default-tertiary-hover: var(--gray-400);
  --background-disabled-default: var(--brand-300);
  --background-neutral-default: var(--slate-700);
  --background-neutral-hover: var(--slate-800);
  --background-neutral-secondary: var(--slate-300);
